@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,100;1,200;1,300;1,400;1,500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600&display=swap);
.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        -webkit-animation: App-logo-spin infinite 20s linear;
                animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

/***********************RESET******************************/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    display: block;
}
body {
    line-height: 1;
}
ol,
ul {
    list-style: none;
}
blockquote,
q {
    quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
    content: "";
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}

* {
    box-sizing: border-box;
}

.flex {
    display: flex;
}

.flex-column {
    display: flex;
    flex-direction: column;
}

.App {
    --light-gray: #ebebeb;
    --light-yellow: #e7cb6c;
    --dark-yellow: #ffbb00;
    --dark-aqua: #679499;
}

.link {
    text-decoration: none;
    cursor: pointer;
    transition: all 0.3s;
}

.link:hover {
    color: var(--dark-yellow);
}

.unselectable {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}


.footer {
    width: 100%;
    background-color: #525252;
    min-height: 160px;
    align-items: center;
    justify-content: space-between;
    padding-left: 15%;
    padding-right: 15%;
}

.footer p {
    font-family: "Poppins", sans-serif;
    color: var(--light-gray);
    font-size: 15px;
    font-weight: 300;
    margin-bottom: 3px;
}

.footer p span {
    font-weight: 500;
}

.footer .block {
    align-items: flex-start;
}

.footer .coffee {
    align-self: center;
    justify-content: center;
    cursor: pointer;
}

.footer .coffee_icon {
    margin-left: 8px;
    cursor: pointer;
    transition: all 0.3s;
    color: #ffffff;
}

.footer .coffee_icon:hover {
    color: var(--dark-yellow);
}

.footer .icons {
    width: 100%;
    margin-bottom: 8px;
    align-items: center;
    justify-content: center;
}

.footer .icon {
    color: #ffffff;
    height: 35px;
    width: 35px;
    transition: all 0.3s;
    cursor: pointer;
}

.footer .icon:hover {
    color: #ffbb00;
}

@media screen and (max-width: 768px) {
    .footer {
        width: 100%;
        margin-left: auto;
        margin-right: auto;
        flex-direction: column;
        padding: 50px 30px;
    }

    .footer .block {
        margin-bottom: 30px;
        text-align: center;
        align-items: center;
    }

    .footer .block:last-child {
        margin-bottom: 0;
    }

    .footer p span {
        color: var(--dark-yellow);
    }

    .footer .icon {
        color: var(--dark-yellow);
    }
}

.home_panel {
    width: 100%;
    height: 100vh;
    position: relative;
    overflow: hidden;
}

.home_panel .b1 {
    position: absolute;
    bottom: 40px;
    left: 40px;
    z-index: 4;
    width: 330px;
    height: 350px;
    background-image: url(/joyceapp/static/media/b1.4ff6d6f6.png);
    background-repeat: no-repeat;
    background-size: cover;
}

.home_panel .b2 {
    position: absolute;
    bottom: 40px;
    left: 40px;
    z-index: 2;
    width: 265px;
    height: 350px;
    background-image: url(/joyceapp/static/media/b2.7e271ece.png);
    background-repeat: no-repeat;
    background-size: cover;
}

.home_panel .b3 {
    position: absolute;
    bottom: 40px;
    left: 40px;
    z-index: 2;
    width: 420px;
    height: 220px;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-size: cover;
}

.home_panel .b4 {
    position: absolute;
    bottom: 40px;
    left: 40px;
    z-index: 2;
    width: 220px;
    height: 350px;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-size: cover;
}

.home_panel .background .overlay {
    position: absolute;
    border-radius: 6px;
    top: 35%;
    z-index: 3;
    height: auto;
    width: 60vw;
    background: #ffffff;
    align-items: flex-end;
    justify-content: center;
    padding: 20px 30px;
}

.home_panel .background .image {
    position: absolute;
    z-index: 2;
    right: 0;
    height: 100vh;
    width: 75vw;
    opacity: 0.7;
    -webkit-clip-path: polygon(39% 0, 100% 0, 100% 100%, 15% 100%);
            clip-path: polygon(39% 0, 100% 0, 100% 100%, 15% 100%);
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.4);
    background-repeat: no-repeat;
    background-size: cover;
}

.home_panel .background .overlay h1 {
    font-family: "Poppins", sans-serif;
    font-weight: 300;
    font-size: 86px;
}

/* .home_panel .background .overlay h2 {
    font-family: "Poppins", sans-serif;
    font-weight: 600;
    font-size: 80px;
    color: cornflowerblue;
} */

@media screen and (max-width: 768px) {
    .home_panel .background .overlay {
        width: 90%;
        right: 10%;
        border-radius: 2px;
        align-items: flex-end;
        justify-content: center;
    }

    .home_panel .background .overlay h1 {
        font-size: 40px;
    }

    .home_panel .background .overlay h2 {
        font-size: 40px;
        text-align: right;
    }

    .home_panel .b1 {
        top: -6%;
        left: -30%;
        transform: scale(0.26);
    }

    .home_panel .b2 {
        top: -10%;
        left: -30%;
        transform: scale(0.26);
    }

    .home_panel .b3 {
        top: 2%;
        left: -32%;
        transform: scale(0.3);
    }

    .home_panel .b4 {
        top: -10%;
        left: -18%;
        transform: scale(0.26);
    }
}

.header {
    position: fixed;
    width: 100%;
    left: 0;
    top: 0;
    z-index: 2000;
    height: 80px;
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.3);
    background-color: #ffffff;
}

.header .logo {
    width: 25vw;
    padding-left: 5vw;
    height: 100%;
    align-items: center;
}

.header .logo img {
    height: 70px;
    width: 70px;
    margin-right: 20px;
    background-color: antiquewhite;
    cursor: pointer;
}

.header .logo .titulo {
    font-size: 18px;
    color: #000000;
    text-decoration: none;
    font-family: "Montserrat", sans-serif;
    text-transform: uppercase;
    cursor: pointer;
}

.header ul {
    height: 100%;
    align-items: center;
    width: 60vw;
}

.header ul .link {
    color: #000;
}

.header ul li {
    font-family: "Montserrat", sans-serif;
    text-decoration: none;
    width: 15vw;
    cursor: pointer;
    transition: all 0.4s;
}

.header ul li:hover {
    color: var(--dark-yellow);
}
@media screen and (max-width: 768px) {
    .header {
        display: none;
    }
}



.portifolio {
    margin-top: 50px;
    min-height: calc(100% - 50px);
    height: auto;
    width: 100%;
}

.portifolio .parte1 {
    width: 100%;
    min-height: 100vh;
    height: auto;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.portifolio .section {
    margin-bottom: 50px;
}

.portifolio .parte1 .box {
    position: relative;
    width: 450px;
    height: 400px;
    margin-left: 15px;
    margin-right: 15px;
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.4);
    border-radius: 4px;
    transition: ease-in-out 0.3s;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.portifolio .parte1 .box img {
    opacity: 0.8;
    width: 55%;
    height: auto;
}

.portifolio .parte1 .box .title {
    bottom: 30px;
    right: 30px;
    font-size: 24px;
    position: absolute;
    color: #ffffff;
    font-weight: bold;
    font-family: "Poppins", sans-serif;
}

.portifolio .description {
    background-color: #e3e3e1;
    font-family: "Poppins", sans-serif;
    text-align: left;
    width: 450px;
    margin-top: 30px;
    padding: 20px;
    border-radius: 4px;
    margin-left: 15px;
    margin-right: 15px;
}

.portifolio .parte1 .box:hover {
    transform: scale(1.01);
    box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.5);
}

.portifolio .parte1 .block_1 {
    background-color: #64adb7;
}
.portifolio .parte1 .block_2 {
    background-color: #ff5d5d;
}
.portifolio .parte1 .block_3 {
    background-color: #ffd955;
}

@media screen and (max-width: 768px) {
    .portifolio {
        margin-top: 150px;
        width: 100%;
    }

    .portifolio .section {
        box-sizing: border-box;
        width: 80%;
        margin-left: 10%;
        margin-right: 10%;
    }

    .portifolio .description {
        width: 100%;
    }

    .portifolio .parte1 .box {
        height: 200px;
        width: 100%;
    }
}


